<template>
    <div class="loginContainer p-10">
        <h2 class="p-5">Login chat Hacendado</h2>
        <select v-model="seleccionado" class="m-4">
            <option value="" disabled selected>Elige un usuario</option>
            <option v-for="user in users" :key="user.id" :value="user.id">
                <p>{{ user.fields.Username }}</p>
            </option>
        </select>
        <router-link :to="{ name: 'list',params: { emisor: seleccionado }}">
            <button v-if="seleccionado != ''" class="text-center mx-auto border border-black p-2 block w-1/2 bg-white text-black hover:bg-black hover:text-white">Entrar</button>
        </router-link>
    </div>
</template>



<script>
export default {
    name : 'login',
    data : function (){
        return{
            users : [],
            seleccionado : ''
        }
    },
    mounted : function(){

        let that = this;

        axios.get('https://api.airtable.com/v0/appXjiwAioymjZHIB/Usuarios?view=Grid%20view')
            .then(function (response) {
            // handle success
            that.users = response.data.records;
        })
        .catch(function (error) {
            // handle error
            console.log(error);
        })
        .then(function () {
            // always executed
        });
    }
}
</script>


<style scoped>
a{
    text-decoration: none;
    color: black;
}
.loginContainer {
    max-width: 500px;
    margin: 0 auto;
    height: auto;
    background-color:#B8CCC3;
}
</style>
