<template>
    <Loading v-if="!loading"></Loading>
    <div class="container" v-else>
        <div class="text-white topContainer">
            <router-link :to="{name : 'login'}">Atras</router-link>
            <h2 class="p-3">Chats activos</h2>
            <div class="searchContainer shadow-lg">
                    <div class="bg-white p-2">
                        <div class="w-4/5 colorGris mx-auto text-center flex items-center border border-white p-2 rounded">
                            <img src="../../public/search.svg" class="w-8">
                            <input type="text" v-model="busqueda" class="inputSearch p-2 w-full ml-3 text-center" placeholder="Búsqueda">
                        </div>
                    </div>
            </div>
        </div>
        <div class="listContainer p-3">
            <div v-for="usuario in arrayFiltrado" :key="usuario.id">
                <router-link :to="{ name: 'chat',params: { emisor: yo, receptor : usuario.id }}" class="chatInfo bg-white flex flex-row mx-auto text-center items-center justify-center p-2 mb-2">
                  <div class="p-2"> 
                      <img alt="usuario" class="imagenChat object-cover" :src="usuario.fields.Imagen">
                  </div>
                  <div class="mx-auto text-left"> 
                      <h5 class="text-2xl">{{ usuario.fields.Username }}</h5>
                      <p>Lorem ipsum dolor sit magnam in reiciendis est</p>
                  </div>
                  <div class="mx-auto text-center"> 
                      <img src="../../public/arrow.svg" class="w-8">
                  </div>
                </router-link>
            </div>
        </div>
      </div>
</template>


<script>

//LOADING 
import Loading from '@/components/Loading.vue';


export default {
    name : 'list',
    components : {
        Loading
    },
    data: function () {
    return {
        arrayUsers : [],
        loading : false,
        yo : '',
        nombre : '',
        busqueda : '',
        prevRuta : ''
      }
    },
    beforeRouteEnter(to, from, next) {

        next(function (vm) {
            vm.prevRuta = from.path;
        });
    }
    ,
    mounted : function(){

      this.yo = this.$route.params.emisor
      this.getUsername(this.yo);

    },
    methods : {
        getAll : function(){

            let that = this;

            axios.get(`https://api.airtable.com/v0/appXjiwAioymjZHIB/Usuarios?view=Grid%20view`)
                .then(function (response) {
                // handle success
      
            for(let user of response.data.records){
                if(user.fields.Username == that.nombre){

                }else{
                    that.arrayUsers.push(user);
                }
            }
            that.loading = true;
        })
        .catch(function (error) {
        // handle error
        console.log(error);
        })
        .then(function () {
        // always executed
        });
    },
    getUsername : function(id_yo){

        let that = this;

            axios.get(`https://api.airtable.com/v0/appXjiwAioymjZHIB/Usuarios/${id_yo}`)
                .then(function (response) {
                // handle success
      
                that.nombre = response.data.fields.Username;
                that.getAll();
        })
        .catch(function (error) {
        // handle error
        console.log(error);
        })
        .then(function () {
        // always executed
        });       
    }
    },computed : {
        arrayFiltrado : function(){

            // BUSCADOR

            let lista = [];
            

            for(let item of this.arrayUsers){
                if(this.busqueda == '' || item.fields.Username.toUpperCase().includes(`${this.busqueda.toUpperCase()}`)){
                    lista.push(item);
                }
            }
            return lista;
        }
    }
}

</script>


