<template>
  <Loading v-if="!loading"></Loading>
  <div class="chat" v-else>
    <div class="userInfo mx-auto text-center pt-10">
      <div class="w-4/5 flex items-center">
        <router-link :to="{name : 'list'}">Atras</router-link> 
        <img :src="imagenReceptor" class="circle object-cover">
          <div class="bg-white p-5 w-full">
            <h5 class="text-2xl pl-3">{{ nombreReceptor }}</h5>
          </div>
      </div>
    </div>
    <div class="chatContainer mx-auto text-center pt-10">
      <div v-for="(mensaje) in enviados" :key="mensaje.id" class="p-4 w-full">
        <div class="flex flex-row-reverse" v-if="mensaje.fields.Emisor == emisor2">
          <p class="w-4/5">{{ mensaje.fields.Texto }} 
            <img  src="../../public/barrachat.svg" class="p-4">
          </p>
        </div>
        <div class="flex flex-row" v-else>
          <p class="w-4/5" >{{ mensaje.fields.Texto }} 
            <img src="../../public/barra2.svg" class="p-4">
          </p>
        </div>
      </div>
      <div class="inputContainer flex flex-row items-center justify-center">
        <input type="text" v-model="textoNuevo" @keyup.enter="sendMensaje" class="p-2 w-4/5 border border-black rounded pin-b pin-r pin-l">
        <button v-if="textoNuevo != ''" @click.prevent="sendMensaje">
          <img src="../../public/send.svg" class="p-2 w-10 border border-black rounded bg-white">
        </button>
      </div>
    </div>
  </div>
</template>


<script>

//LOADING 
import Loading from '@/components/Loading.vue';

export default {
  name : 'chat',
  components : {
    Loading
  },
  props : ['emisor','receptor'],
  data : function(){
    return {
      receptor2: 0,
      emisor2 : 0,
      nombreEmisor : '',
      nombreReceptor : '',
      imagenReceptor : '',
      enviados : [],
      loading : false,
      textoNuevo : '',
      tiempoRefresco : 10000,
      prevRuta : ''
    }
  },
    beforeRouteEnter(to, from, next) {

        next(function (vm) {
            vm.prevRuta = from.path;
        });
    }
  ,mounted : function(){

      let that = this;

      this.receptor2 = this.$route.params.receptor;
      this.emisor2 = this.$route.params.emisor;

      this.getAll(this.emisor2,this.receptor2);
      setInterval(this.refreshMessages,this.tiempoRefresco);
  },
  methods : {
    getAll(id_emisor,id_receptor){

      let that = this;
      
      //Obtenemos emisor

      axios.get(`https://api.airtable.com/v0/appXjiwAioymjZHIB/Usuarios/${id_emisor}`)
      .then(function (response) {
        that.nombreEmisor = response.data.fields.Username;

          //Obtenemos receptor

          axios.get(`https://api.airtable.com/v0/appXjiwAioymjZHIB/Usuarios/${id_receptor}`)
        .then(function (response) {

          that.nombreReceptor = response.data.fields.Username;
          that.imagenReceptor = response.data.fields.Imagen;

          //Cogemos todos los mensajes entre las dos partes

          that.refreshMessages();
        })
        .catch(function (error) {
          // handle error
          console.log(error);
        })
        .then(function () {
        // always executed
        });
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
      .then(function () {
      // always executed
      });
    },
    sendMensaje : function(){
        
        let that = this;

        axios.post('https://api.airtable.com/v0/appXjiwAioymjZHIB/Mensajes',{
        "fields": {

          "Texto" : that.textoNuevo,
          "Emisor" : [that.emisor2],
          "Receptor" : [that.receptor2]
        }
      })
        .then(function (response) {
          // handle success
          that.refreshMessages();
          that.textoNuevo = '';
        })
        .catch(function (error) {
          // handle error
        console.log(error);
        })
        .then(function () {
        // always executed
        });
    },
    refreshMessages : function(){

      let that = this;

          axios.get(`https://api.airtable.com/v0/appXjiwAioymjZHIB/Mensajes?view=Grid%20view&filterByFormula=OR(AND({Emisor}="${that.nombreEmisor}", {Receptor}="${that.nombreReceptor}"), AND({Emisor}="${that.nombreReceptor}", {Receptor}="${that.nombreEmisor}"))`)
            .then(function (response) {

          if(response.data.records.length != that.enviados.length){

            that.enviados =  response.data.records;

            that.loading = true;
          }
    })
    .catch(function (error) {
      // handle error
      console.log(error);
    })
    .then(function () {
      // always executed
    });
    }
  },updated :function(){
      window.scrollTo(0,document.body.scrollHeight);
  }
}
</script>

<style scoped>
h5{
    font-weight: normal;
}

.chat{
    max-width: 500px;
    margin: 0 auto;
    height: auto;
    background-color: #B8CCC3;
}

.chatLoading{
  max-width: 500px;
  margin: 0 auto;
  height: auto;
  background-color: rgba(0,0,0,.6);
}

.userInfo{
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: right;
}

.circle{
  border: 3px solid grey;
  background-color: white;
  border-radius: 50%;
  width: 100px;
  height: 100px;
}

</style>
